import { useEffect, useState } from "react";
import { useNavigate } from "react-router-dom";
import ProductInput from "../../components/ProductInput/ProductInput";
import SaveTopBar from "../../components/TopBar/SaveTopBar/SaveTopBar";
import useFetch from "../../hooks/useFetch";
import useImageHandler from "../../hooks/useImageHandler";
import { Section, ImageLabel, Preview, UploadLabel } from "./styledProduct";

export default function Product() {
  const { postData, isPending } = useFetch();
  const { imageRef, preview, previewHandler, imageUploadHandler } =
    useImageHandler();

  const [itemName, setItemName] = useState(""); // 
  const [itemPrice, setItemPrice] = useState(""); // 
  const [itemLink, setItemLink] = useState(""); //  
  const [valid, setValid] = useState(false); //   
  const navigate = useNavigate();

  const itemNamehandler = (event) => {
    //    
    setItemName(event.target.value);
  };

  const itemPricehandler = (event) => {
    const value = event.target.value;
    //    
    const test = new RegExp("^[0-9]+$", "g");
    if (value === "") setItemPrice("");
    else if (test.test(value)) setItemPrice(value);
    else return;
  };

  const itemLinkhandler = (event) => {
    //     
    setItemLink(event.target.value);
  };

  const submitHandler = (event) => {
    if (+itemPrice === 0) {
      alert(" 1  !");
      return;
    }
    event.preventDefault(); // submit    

    imageUploadHandler(imageRef.current.files[0])
      .then((res) => {
        const body = {
          product: {
            itemName: itemName,
            price: +itemPrice,
            link: itemLink,
            itemImage: `https://mandarin.api.weniv.co.kr/${res?.filename}`,
          },
        };

        postData("/product", body)
          .then(() => navigate("/profile", { replace: true }))
          .catch((err) => console.log(err));
      })
      .catch((err) => {
        //   
        alert(err);
      });
  };

  useEffect(() => {
    if (
      preview &&
      itemName.length > 0 &&
      itemName.length < 16 &&
      itemPrice.length > 0 &&
      itemPrice.length < 16 &&
      itemLink.length > 0
    ) {
      setValid(true);
    } else {
      setValid(false);
    }
  }, [preview, itemName, itemPrice, itemLink]);

  return (
    <>
      <SaveTopBar
        handler={submitHandler}
        move="/profile"
        isPending={isPending}
        valid={valid}
      />
      <Section>
        <ImageLabel> </ImageLabel>
        <Preview>
          {preview ? <img src={preview} /> : ""}
          <UploadLabel>
            <input
              ref={imageRef}
              type="file"
              accept="image/*"
              onChange={previewHandler}
              style={{ display: "none" }}
            />
          </UploadLabel>
        </Preview>
        <p>{preview ? null : " ."}</p>
        <ProductInput
          label=""
          placeholder="1~15  ."
          stateInp={itemName}
          handler={itemNamehandler}
          inptype="text"
          min="2"
          max="15"
        />
        <ProductInput
          label=""
          placeholder="  ."
          stateInp={itemPrice}
          handler={itemPricehandler}
          inptype="text"
          max="15"
        />
        <ProductInput
          label=" "
          placeholder="URL  ."
          stateInp={itemLink}
          handler={itemLinkhandler}
          inptype="text"
        />
      </Section>
    </>
  );
}
