import { useEffect, useRef, useState } from "react";
import { useNavigate } from "react-router-dom";
import SetProfile from "../../components/common/SetProfile/SetProfile";
import SaveTopBar from "../../components/TopBar/SaveTopBar/SaveTopBar";
import useFetch from "../../hooks/useFetch";
import useImageHandler from "../../hooks/useImageHandler";
import useUserContext from "../../hooks/useUserContext";

export default function ProfileEdit() {
  const { user, dispatch } = useUserContext();
  const { putData, isPending } = useFetch();
  const { imageRef, imageUploadHandler } = useImageHandler();

  //   
  const navigate = useNavigate();

  const [username, setUsername] = useState(user?.username); //  
  const [accountname, setAccountname] = useState(user?.accountname); //  ID
  const [intro, setIntro] = useState(user?.intro); // 
  const [valid, setValid] = useState(false); // 

  //    
  const submitHandler = async (event) => {
    event.preventDefault(); // submit   

    imageUploadHandler(imageRef.current.files[0])
      .then((res) => {
        //    body   
        const body = {
          user: {
            username: username,
            accountname: accountname,
            intro: intro,
            image: res
              ? `https://mandarin.api.weniv.co.kr/${res.filename}`
              : user.image,
          },
        };

        putData("/user", body)
          .then((res) => {
            //     
            console.log("put  :", res);
            dispatch({ type: "LOGIN", payload: res.user });
          })
          .then(() => navigate("/profile", { replace: true }))
          .catch((err) => {
            console.log(err);
          });
      })
      .catch((err) => {
        //   
        alert(err);
      });
  };

  useEffect(() => {
    if (!user) navigate("/");
  }, [user]);

  return (
    <>
      <SaveTopBar handler={submitHandler} isPending={isPending} valid={valid} />
      <SetProfile
        join={false}
        username={username}
        accountname={accountname}
        intro={intro}
        imageRef={imageRef}
        setUsername={setUsername}
        setAccountname={setAccountname}
        setIntro={setIntro}
        setValid={setValid}
      />
    </>
  );
}
